tr.test-placeholder {
  margin: 5px;
}
tr td {
  padding: 5px;
  width: 10vw;
  height: 5vh !important;
}
/**/
tr.test-placeholder td:before {
  position: absolute;
  width: 95%;
  height: 95%;
  background: #d6d6d6;
  content: "";
  top: 2.5%;
  left: 2.5%;
  border-radius: 10px;
  z-index: 2;
  opacity: 0.4;
}

tr.test-placeholder td {
  position: relative;
  opacity: 0;
}

tr.test-placeholder {
  overflow: hidden !important;
  position: relative;
}

tr.test-placeholder:after {
  width: calc(100% - 20px);
  height: 90%;
  background: #c7c7c7;
  position: absolute;
  top: 50%;
  content: "";
  z-index: 2;
  opacity: 1;
  filter: blur(1.5px);
  left: 50%;
  transform: translate(-50%, -50%);
  background: linear-gradient(
    90deg,
    rgba(227, 227, 227, 0.6) 0%,
    rgba(200, 200, 200, 0.8) 50%,
    rgba(227, 227, 227, 0.6) 100%
  );
  background-position: 150vw 0;
  animation: placeholderblur 3s linear infinite;
}
@keyframes placeholderblur {
  0% {
    background-position: -150vw 0;
    opacity: 0.9;
  }
  50% {
    opacity: 0.9;
  }
  100% {
    background-position: 200vw 0;
    opacity: 0.9;
  }
}
